    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
  .nav--closed .nav__burger::before,
  .nav--closed .nav__burger::after {
    top: 0;
    left: 0;
    background-color: #f9fbfd;
    display: block;
    position: absolute;
    content: "";
