    height: 2px;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
  .nav--closed .nav__burger::before {
    transform: translate(0, -8px); }
  .nav--closed .nav__burger::after {
    transform: translate(0, 8px); }
  .nav--opened .nav__burger {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    background-color: transparent;
    transform: translate(-50%, -50%);
    width: 70%;
    height: 2px;
    transition-timing-function: ease-in-out;
    transition-duration: 0.3s; }
    .nav--opened .nav__burger::before, .nav--opened .nav__burger::after {
      background-color: #f9fbfd;
      display: block;
      position: absolute;
      content: "";
