    .nav--opened .nav__burger::after {
      transform: rotate(-45deg); }
  .nav--closed .nav__list {
    display: none; }
  .nav--nojs .nav__toggle {
    display: none; }
  .nav--nojs .nav__list {
    display: block; }
  .nav__list {
    flex-direction: column; }
  .nav__item {
    align-items: flex-start; }
    .nav__item:not(:last-child) {
